import React from "react";

import './styles.sass'

const Card = ({ img, stage_name, name, profesion, description  }) => {
    return(
        <div className='card-container'>
            <img src={`./assets/${img}`} alt='img'/>
            <div className='content'>
                <div className='title'>{stage_name}</div>
                <div className='specyfic'>{`${name} | ${profesion}`}</div>
                <div className='description'>{description}</div>
            </div>
        </div>
    )
}

export default Card