export const formatString = (string) => {
    const stringArray = []
    const responseArray = []
    for(let i = 0; i <string.length; i++){
        stringArray.unshift(string[i])
    }
    for(let y = 0; y <stringArray.length; y++){
        responseArray.unshift(stringArray[y])

        if((y+1)%3 === 0 && (y + 1) !== stringArray.length){
            responseArray.unshift(' ')
        }
    }
    
    return responseArray.join('')
}