import React, { useState, useEffect } from "react";
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import { useNavigate } from "react-router";
import 'react-loading-skeleton/dist/skeleton.css'

import PodcastComponent from "../../components/PodcastComponent/PodcastComponent";
import { authStore } from "../../store/authStore";
import './styles.sass'

const Podcasts = () => {
    const [podcasts,setPodcasts] = useState([])
    const navigate = useNavigate()

    useEffect(()=>{
        (async()=>{
            const response = await authStore.fetchYoutubePlaylist()
           
            const podcastsArray = []

            for (const podcast of response.items){
                const video = await authStore.fetchYoutubeVideo(podcast.snippet.resourceId.videoId)
                console.log(video)
                podcastsArray.unshift({
                    title: video.items[0].snippet.localized.title,
                    embedId: video.items[0].id
                })
            }

            setPodcasts(podcastsArray)
        })()
    },[])

    return(
        <div className='podcasts-container'>
            <div className='podcast-info'>
                <div className='title'>
                    <img alt='logo' src='./assets/logo.png'/>
                    <div>NotSoTam | Podcast</div>
                </div>
                <div className='content'>
                    <img alt='img' src='./assets/test.png'/>
                    <div className='text'>
                        <div>
                            NotSoTam Podcast to odcinki realizowane w formie wywiadu z modymi tw贸rcami, kt贸rzy w wolnym czasie oddaj si
                            produkcji audio, krceniu teledysk贸w, organizacj merch'贸w i innym czynnociom przemysu muzycznego. 
                            Bdziecie mogli dowiedzie si co dzieje si za kulisami, jak powstaj najwiksze hity i jaki
                            ogrom pracy kryje si za ka偶dym utworem.
                        </div>
                        <div className='button' onClick={()=> navigate('/contact')}>
                            Chciaby wzi udzia w naszym podcascie? Zapraszamy do kontaktu 
                        </div>
                    </div>
                </div>
            </div>

            {
                podcasts.map((podcast,index) =>
                    <PodcastComponent 
                        embedId={podcast.embedId}
                        title={podcast.title}
                        key={podcast.title}
                        index={index}
                    />
                )
            }
            {
                podcasts.length > 0 ?
                null
                :
                <SkeletonTheme baseColor="#FFE5DF" highlightColor="#D5D5D5">
                    <Skeleton width='70vw' height='450px' count={5}/>
                </SkeletonTheme>
            }
            
        </div>
    )
}

export default Podcasts