import React, { useState, useEffect } from 'react';

import './styles.sass'

const Intro = ({setState}) => {
    const [clicked,setClicked] = useState(false)
    const [showHint, setShowHint] = useState(false)

    const handleClick = () => {
        setShowHint(false)
        setClicked(true)

        window.setTimeout(()=>{setState(true)},1700)
    }

    useEffect(()=>{
        window.setTimeout(()=> {setShowHint(true)},20000)
    },[])

    return (
        <div className='intro-container'>
            {
                showHint ?
                    <div className='hint'>Kliknij w pudeko!</div>
                : null
            }
            <img 
                onClick={()=> handleClick()}
                src='./assets/logo.png' 
                style={clicked ? {marginLeft: '0%', transition:'1.5s', animation: 'none'} : {marginLeft: '30%'}}
                alt='logo' 
            ></img>
            <div 
                className={`${clicked ? 'before' : 'after'} text`}
            >Suchanie zabija</div>
        </div>
    );
}

export default Intro;