import dotenv from  'dotenv'
import { Navigate } from 'react-router'
export const user = { athenticated: false }

export const RequireAuth = ({children}) =>  {
    if (!user.athenticated) { 
        return(
            <Navigate to='/login'></Navigate>
        )
    } else {
        return children
    }
}

