const youtubeQueries = {
    base: 'https://youtube.googleapis.com/youtube/v3/',
    playlistId : 'PLRAxOu_VQ0lDJNcV8BsAvuI3vXs9ONDgw',
    artistId : 'UCDueDKhUKElrWqEUl7wqXoQ',
}


const fetchYoutubeArtist = async() => {
    const promise = await fetch(`${youtubeQueries.base}channels?id=${youtubeQueries.artistId}&part=snippet,statistics&key=${process.env.REACT_APP_YT_API_KEY}`,{
        method: 'GET',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        },
    })
    const response = await promise.json()

    return response
}

const fetchYoutubePlaylist = async() => {
    const promise = await fetch(`${youtubeQueries.base}playlistItems?playlistId=${youtubeQueries.playlistId}&part=snippet&key=${process.env.REACT_APP_YT_API_KEY}`,{
        method: 'GET',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        },
    })
    const response = await promise.json()

    return response
}

const fetchYoutubeVideo = async(id) => {
    const promise = await fetch(`${youtubeQueries.base}videos?part=snippet&id=${id}&key=${process.env.REACT_APP_YT_API_KEY}`,{
        method: 'GET',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        },
    })
    const response = await promise.json()

    return response
}

const fetchInstagramPosts = async() => {

    const promise = await fetch(`https://graph.instagram.com/me/media?fields=id,caption,media_url,media_type&access_token=${process.env.REACT_APP_INSTAGRAM_USER_TOKEN}`,{
        method: 'GET',
        headers:{
            "Access-Control-Allow-Origin": "*"
        }
    })

    const response = await promise.json()

    return response.data
}


export const authStore = { user, fetchYoutubeArtist, fetchYoutubePlaylist, fetchYoutubeVideo, fetchInstagramPosts}