import React from "react";

import { contactInfo } from "../../store/store"
import './styles.sass'

const Contact = () => {
    return(
        <div className='contact-container'>
            <div className="info-wrapper">
                <div className="title">
                    Chcesz nawiza wsp贸prace, zgosi si do podcastu, a mo偶e wysa nam swoj prewk? Zapraszamy do kontaktu.
                </div>
                {
                    contactInfo.map(info =>
                    <div className="element" key={info.name}>
                        <div className="label">
                            <img src={`./assets/${info.icon}`} alt="icon"/>
                            <div className="name">{`${info.name}:`}</div>
                        </div>
                        {
                            info.name === 'Nr telefonu' ?
                            <div className="text" style={{padding: '4px 0'}}>791 475 000</div> :null
                        }
                        <div className="text">{info.text}</div>
                    </div>
                )
                }
            </div>
        </div>
    )
}

export default Contact