import React, { useState } from 'react';

import { BrowserRouter } from 'react-router-dom';
import RoutesHandler from './pages/RoutesHandler';

import DesktopMenu from './pages/Menu/DesktopMenu';
import MobileMenu from './pages/Menu/MobileMenu';
import Spacer from './components/Spacer';
import Footer from './pages/Footer/Footer';
import Intro from './pages/Intro/Intro';

import './main.sass'

const App = () => {
    const [state, setState] = useState(false)
    return (
        <div className='main-container' style={state ? {} : {height: '100vh',overflow: 'hidden'}}>
            {
                !state ? <Intro setState={setState}/> : null
            }
            <BrowserRouter>
            <DesktopMenu />
            <MobileMenu/>
            {state ? <Spacer/> : null}
            <RoutesHandler/>
            {state ? <Footer/> : null}
            </BrowserRouter>
        </div>
    )
}

export default App