import React, { useState } from "react"

import Skeleton,{ SkeletonTheme } from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
import './styles.sass'

const PodcastComponent = ({ index, title, embedId }) => {
    const [isLoaded,setIsLoaded] = useState(false)

   console.log()

    return(
        <div className={`podcast-container ${index === 0 ? 'first' : ''}`} key={title}>
            <div 
                className='title'
            >{title || <Skeleton width='300px' height='40px'/>}</div>
            <iframe 
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                style={isLoaded ? {} : { display: 'none' }}
                onLoad={()=>setIsLoaded(true)}
                src={`//www.youtube.com/embed/${embedId}`}
                className='video' 
                allowFullScreen
                frameBorder="0"
                title="yt" 
            ></iframe>
            {
                isLoaded ?
                null
                :
                <SkeletonTheme baseColor="rgba(0,0,0,0.1)" highlightColor="rgba(0,0,0,0.2)">
                    <Skeleton width='600px' height='335px'/>
                </SkeletonTheme>
            }
        </div>
    )
}

export default PodcastComponent