import React, { useState } from "react";

import { mediaLinks } from '../../store/store'
import './styles.sass'

const InfoCard = ({ closeCard, cardContent }) => {
    const [mainImg, setMainImg] = useState(cardContent.imgs[0])

    return(
        <div className='info-card-container'>
            <div className='content'>
                <img 
                    onClick={()=> closeCard(false)}
                    className='close-button'
                    src='./assets/close.svg'
                    alt='icon'
                />
                <div className='gallery'>
                    <img src={mainImg} alt='mainImg' className='mainImg'/>
                    <div className='imgs-wrapper'>
                        {
                            cardContent.imgs.map(src =>
                                <img 
                                    onClick={()=>setMainImg(src)}
                                    src={src} 
                                    alt='img'
                                />)
                        }
                    </div>
                </div>

                <div className='purchase'>
                    <div className='purchase-button'>KUP</div>

                    <div className='info'>
                        Hej, bardzo si cieszymy 偶e jeste zainteresowany zakupem naszej czapki,
                        nasza strona internetowa stale sie rozwija i intensywnie pracujemy nad stworzeniem
                        dla was wygodnej metody dokonywania zakup贸w, natomiast niestety na ten moment prosimy
                        aby w sprawie zakupu napisa do nas na instragramie na priv.
                    </div>
                    
                    <img 
                        onClick={()=> window.open(mediaLinks[0].to,'_blank')}
                        src='./assets/instagram.svg'
                        alt='icon'
                    />
                </div>
            </div>
        </div>
    )
}

export default InfoCard