import React, { useState, useEffect } from "react";

import Banner from "../../components/Banner/Banner";
import ShopItem from "../../components/ShopItem/ShopItem"
import InfoCard from "../../components/InfoCard/InfoCard"
import './styles.sass'

const Shop = () => {
    const [showMessage, setShowMessage] = useState(false)
    const [cardContent, setCardContent] = useState({})
    const [products,setProducts] = useState([])

    useEffect(()=>{
        const response = [
            {_id: '1234',name:'Czapka z logo',price:'50z', imgs:['./assets/czapka-logo-1.png','./assets/czapka-logo-2.jpeg','./assets/czapka-logo-3.jpeg'], quantity: 10, description: 'opis czapki'},
            {_id: '1243',name:'Czapka z napisem',price:'50z', imgs:['./assets/czapka-napis-1.png','./assets/czapka-napis-2.jpeg','./assets/czapka-napis-3.jpeg'], quantity: 10, description: 'opis czapki'},
        ]

        setProducts(response)
    },[])

    const handleBuy = (item) => {
        setShowMessage(true)
        setCardContent(item)
    }

    return (
        <div className='shop-container'>
            <Banner/>

            {showMessage ?   
                <InfoCard closeCard={setShowMessage} cardContent={cardContent}/>
            :null}

            <div className='search-bar'>
                <div className='search'>
                    <div className='label'>Wyszukaj produkt:</div>
                    <input type='text' />
                    <img alt='loop' src='./assets/search.svg'/>
                </div>

                <div className='filter'>
                    <div className='label'>Filtruj:</div>
                    <select name="filter">
                        <option value="price-high">Cena: od najni偶szej</option>
                        <option value="price-low">Cena: od najwy偶szej</option>
                    </select>
                </div>

            </div>
            <div className='items-wrapper'>
                    {
                        products.map(product =>
                            <ShopItem 
                                description={product.description}
                                quantity={product.quantity}
                                handleBuy={handleBuy}
                                price={product.price}
                                imgs={product.imgs}
                                name={product.name}
                                key={product._id}
                                id={product._id}
                            />  
                        )
                    }
                </div>
        </div>
    )
}
export default Shop