import React, { useState } from "react";
import { Link } from 'react-router-dom'

import './styles.sass'
import { menuLinks, mediaLinks } from "../../store/store";

const MobileMenu = () => {
    const [showMenu, setShowMenu] = useState(false)

    const handleClick = (to) => {
        window.open(to,'_blank')
    }

    return (
        <div 
            className='mobile-menu-container'
            style={{height: showMenu ? `${(menuLinks.length + 2)* 100}px`:'50px'}}
        >
            <div className='header'>
                <img src='./assets/logo.png' alt='logo'/>
                <img src='./assets/menu.svg' alt='menu' onClick={()=> setShowMenu(!showMenu)}/>
            </div>
            {
                showMenu ?
                <div className='menu'>
                    {
                        menuLinks.map(link=>
                            <Link 
                                className='link'
                                onClick={()=>setShowMenu(false)}
                                key={link.name}
                                to={link.to} 
                            >{link.name}</Link>
                        )
                    }
                    <div className='media-link-wrapper'>
                        {
                            mediaLinks.map( link =>
                            <img 
                                onClick={()=> handleClick(link.to)}
                                src={`./assets/${link.icon}`}
                                className='icon-link' 
                                key={link.name} 
                                alt={link.name}
                            />
                        )
                        }
                    </div>
                </div>
                :null
            }
        </div> 
    );
}

export default MobileMenu;