import React, { useRef } from "react";
import { toast, ToastContainer } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

import 'react-toastify/dist/ReactToastify.css';
import './styles.sass'

import { authStore } from '../../store/authStore'

const Login = () => {
    const email = useRef()
    const password = useRef()

    const navigate = useNavigate()

    const login = () => {
        if(email.current.value === ''){
            toast.error('Email jest wymagany',{
                position: "top-right",
                theme: 'dark',
            })
        } else if (password.current.value === '') {
            toast.error('Haso jest wymagane',{
                position: "top-right",
                theme: 'dark',
            })
        } else {
            (async () => {
                const response = await authStore.fetchData('login',{
                    email: email.current.value,
                    password: password.current.value,
                },'POST')

                if (response.status) {
                    authStore.user.athenticated = true
                    toast.success('zalogowano',{
                        position: "top-right",
                        theme: 'dark',
                    })
                    navigate('/dashboard/artykuly')
                } else {
                    toast.error(response.message,{
                        position: "top-right",
                        theme: 'dark',
                    })
                }
            })()
        }
    }

    return (
        <div className='login-container'>
            <div className='login-window'>
                <div className='title'>Zaloguj si</div>

                <div className='label'>mail</div>
                <input type='text' ref={email}></input>

                <div className='label'>haso</div>
                <input type='text' ref={password}></input>

                <div className='button' onClick={login}>ZALOGUJ</div>
                <ToastContainer/>
            </div>
        </div>
    )
}

export default Login