import React from 'react';

import './styles.sass'

const ShopItem = ({id,name, imgs, quantity,price, description, handleBuy}) => {
    const item = {id,name, imgs, quantity,price, description}
    return (
        <div className='shop-item-container'>
            <img alt='img' src={imgs[0]}/>
            <div className='info'>
                <div className='name'>{name}</div>
                <div className='price'>{price}</div>
                <div 
                    className='button'
                    onClick={()=> handleBuy(item)}
                >KUP</div>
            </div>
        </div>
    );
}

export default ShopItem;