import React from "react";
import { useParams } from "react-router";
import { Link } from "react-router-dom";

import './styles.sass'

import Accounts from "./Accounts";

const Dashboard = () =>{
    const routeParams = useParams()
    
    return(
        <div className='dashboard-container'>
            <div className='side-menu'>
                <Link className='menu-bar' to="/dashboard/slider">Slider</Link>
                <Link className='menu-bar' to="/dashboard/artykuly">Artykuly</Link>
                <Link className='menu-bar' to="/dashboard/konta">Konta</Link>
            </div>
            <div className='content'>
                {
                    routeParams.card === 'slider' ? <div>Slider</div> : null
                }
                {
                    routeParams.card === 'artykuly' ? <div>Artykuly</div> : null
                }
                {
                    routeParams.card === 'konta' ? <Accounts></Accounts> : null
                }
            </div>
        </div>
    )
}

export default Dashboard